.home {
  
  width: 100vw;
  padding: 3rem;
  margin: 0rem auto;
  text-align: center;
  font-family: 'Antic', sans-serif;
  height: 100% ;
  font-size: 2rem ;
  
  background: linear-gradient(180deg, rgba(6,9,23,1) 0%, rgba(11,19,56,1) 51%, rgba(8,22,88,1) 100%);
}

.home h1 {
  font-size: 5rem ;
  color:white;
}

.home p {
  font-size: 2rem ;
  color:white;
}
.home img {
  position: fixed;
  top: 1;
  left: 0;
  width: 100%;
  max-width: 20rem;
}

.away {
  width: 40rem ;
  max-width: 90%;
  border-radius: 10px;
  padding: 3rem;
  margin: 2rem auto;
  text-align: center;
  font-family: 'Antic', sans-serif;
  color: white ;
  background: linear-gradient(180deg, rgba(0,0,0,0.9332107843137255) 0%, rgba(0,30,66,1) 49%, rgba(0,0,0,0.9472163865546218) 100%);
}
.away p {
  font-size: 1.5rem ;
}
.button {
  font-family: 'Antic', sans-serif;
  border: 1px solid #060917;
  background: #060917;
  color: white;
  padding: 0.75rem 3.5rem;
  cursor: pointer;
  font-size: 1.5rem;
  border-radius: 30px;
  transition: all 0.5s;
  cursor: pointer;
  text-transform: uppercase;
}


.button:hover {
  font-family: 'Antic', sans-serif;
  border: 1px solid black;
  background: yellow;
  color: black;
  padding: 0.75rem 3.5rem;
  cursor: pointer;
  font-size: 2rem;
  border-radius: 30px;
  
}

@media screen and (max-width: 600px) {
  .home img {
    position: sticky;
    top: 1;
    left: 0;
    width: 100%;
    max-width: 6rem;
  }
  .away {
    visibility: hidden ; 
  }
  .home h1 {
    font-size: 3rem ;
    color:white;
  }
  
  .home p {
    font-size: 1rem ;
    color:white;
  }
}