* {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  background: #fff;
}

body {
  margin: 0;
 
}

main {
  margin-top: 1rem;
}

.main-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background:rgba(6,9,23,1) ;
  padding: 0 2rem;
}

.main-header h1 {
  color: white;
  font-family: 'Sintony', sans-serif;
  font-size: 2rem ;
  align-items: center;
}

.nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}

.nav li {
  margin: 0;
  margin-left: 2rem;
  font-family: 'Tenor Sans', sans-serif;
}

.nav a {
  text-decoration: none;
  color: white;
}

.nav a:hover,
.nav a:active {
  color: #f3cafb;
}
@media screen and (max-width: 600px) {
  .main-header h1 {
    color: white;
    font-family: 'Sintony', sans-serif;
    font-size: 1rem ;
    align-items: center;
  }
  
}
